.startTrialContainer{
    width: 100vw;
    
    
}
.heading{
    
    margin-top:20px;
    
    font-size: 50px;
    font-weight: 900;
    color: rgba(125, 125, 125, 1);
    text-align: center;
}

  
.changeWork{
    font-size: 20px;
    font-weight: 600;
    color: rgba(125, 125, 125, 1);
    margin-right: 20%;
    display:inline;

}

.changeWork:hover{
    cursor: pointer;
}

.hiwLine{
    margin-left:18%;
    margin-right: 25%;
    font-size: 20px;
    font-weight: 600;
    color: rgba(125, 125, 125, 1);
    

}
.hiw-tab-box{
    margin-left: 18%;
    margin-right: 10%;
}

.tab-link-container{
    height: 26px;
    border-bottom: 2px solid rgba(151, 151, 151, .5);
}

.circle{
    height:101px;
    
    width: 101px;
    border: 5px solid rgba(125, 125, 125, 1);
    border-radius: 50%;
    float: left;
    text-align: center;
    
}

.line1{
    position: absolute;
    height: 80px;
    width: 2px;
    background: rgba(128, 128, 128, 0.301);
    left: 50px;
    top: 162px;
}
.line2{
    position: absolute;
    height: 80px;
    width: 2px;
    background: rgba(128, 128, 128, 0.301);
    left: 50px;
    top: 362px;
}

.hello{
    float:left;
    padding-left: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
#number1{
    
    margin-top:25%;
    font-size: 30px;
    font-weight: 600;
    color: rgba(127, 127, 127, 1);
}
.circle1{
    height:101px;
    
    width: 101px;
    border: 5px solid rgba(125, 125, 125, 1);
    border-radius: 50%;
    
    text-align: center;
    
}
.pop{
    display: inline-block;
}


.active{
    border-bottom: 2px solid rgba(0, 200, 136, 1);
}

.hiwContent{
    padding-top: 50px;
    position: relative;
}

.step{
    display: flex;
    margin-bottom: 100px;
}

.bullet{
    margin: 0px;
    font-size: 25px;
}

.setup{
    margin: 0px;
}

.credit-card{
    position: absolute;
    width: 300px;
    height: 170px;
    right: 0px;
    top: 50%;
    transform: translateY(-55px);
    border-radius: 4px;
    background-color: gold;
}
.credit-card-design{
    position: absolute;
    width: 300px;
    height: 170px;
    right: 0px;
    top: 50%;
    transform: translateY(-55px);
    border-radius: 4px;
    background-color: grey;
    clip-path: polygon(0 0, 20% 0, 80% 100%, 0 100%);
}

.credit-card-content{
    color: white;
    position: absolute;
    width: 300px;
    height: 170px;
    right: 0px;
    top: 50%;
    transform: translateY(-55px);
    border-radius: 4px;


}

