.overviewContainer{
    width: 100vw;
    margin-top: 0px;

}
.searchBar{
    
    flex: 2 2 20px;
    margin-left: 18px;
    padding: 20px 0px;
    display: flex;

}
.searchInput{
    width: 30%;
    margin: 0 285px;
    border: 1px solid rgba(0, 0, 0, 0.185);
    border-radius: 4px;
}

.overviewContent{
    display:flex;
    border-top: 1px solid  rgba(0, 0, 0, 0.185);

}
.overviewContentLeft{
    flex: 2 2 20px;
    padding: 50px 20px;
    border-right: 1px solid  rgba(0, 0, 0, 0.185);

}
.overviewContentRight{
    display: flex;
    flex-wrap: wrap;
    flex: 8 8 80px;
}

.cardContainer{
    width: 33%;
    border-right: 1px solid rgba(0, 0, 0, 0.185);
    border-bottom: 1px solid rgba(0, 0, 0, 0.185);

}
.cardCover{
    width:60%;
    
    margin-left:20%;
    padding: 10%;
    background: rgba(0, 0, 0, 0.03);
    margin-top: 30px;
}
.cardCover img{
    width:50%;
    margin-left:25%;
    
}
.cardDetails{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;

}

.cardDetails div, a{
    margin: 0 auto;
}

.yellow{
    color: rgba(247, 181, 0, 1);
    font-size: 22px;
}
.fonts{
    color:rgba(0, 0, 0, 1);
    font-size: 20px;
    font-weight: 300;
    cursor:pointer;
    
}
.bottom{
    background: rgba(33, 33, 33, 1);
    padding: 20px 21%;
    
}
.text{
    color: white;
    font-size:20px;
    font-weight:300;
    
}
.contact{
    font-size:20px;
}
.textSmall{
    color: white;
    font-size:8px;
    font-weight:300;
}
.last{
    background: grey;
}
.homepage{
    display: inline;
}
.text1{
    display: inline;    
    color:white

}
.links{
    text-align: left;
    color: white;
    font-size:18px;
    
}
.lastLine{
    border: 1px solid rgba(151, 151, 151, 1);
    font-weight: 300;
    
}
.country{
    color: white;
    font-size: 10px;
}
.copyrightYear{
    color: white;
    font-size: 8px;

}